/*
    ===============================
            Work Styles
    ===============================
*/
import styled from 'styled-components';

export const WorkDiv = styled.div`
    height: auto;
    padding: 50px 0;
    overflow: hidden;
    background: #fff
    `,

WorkTitle = styled.h2`
    font-size: 60px; 
    margin: 0;
    `,

Span = styled.span`
    font-weight: normal
    `,

Part = styled.div`
    margin-top: 20px;
    width: 30%;
    float: left;
    height: auto;
    padding: 100px 0;
    border: 1px solid #888;
    box-sizing: border-box;
    text-align: center;
    margin-left: 5%;
    margin-left : ${props => props.first === 1 ? '0' : '5%'}
    `,

Icon = styled.i`
    color: #888;
    margin-bottom: 20px
`,

PartTitle = styled.h4`
    font-size: 25px;
    color: #eb5424;
    margin-bottom: 20px
    `,

Line = styled.hr`
    width: 60%;
    margin: auto;
    margin-bottom: 20px
    `,

PartDetails = styled.p`
    font-size: 14px;
    color: #888;
    padding: 20px
`;
